<template>
    <div>
        <Row>
            <Button type="primary" @click="add" v-permission.show="'b:perm:user:add'">
            <Icon type="plus"></Icon>新增员工</Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">昵称：</label>
            <Input v-model="table.query.nickname" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">手机：</label>
            <Input v-model="table.query.phone" placeholder="" clearable style="width: 200px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="cols" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <div style="height: 280px">
                <vue-scroll>
                    <Form ref="form" :model="vo" :label-width="80" :rules="validate">
                        <Form-item label="手机号" prop="user.username">
                            <Input v-model.trim="vo.user.username" />
                        </Form-item>
                        <Form-item label="密码" prop="user.password">
                            <Input class="margin-right-10" style="width:60%" v-show="isUpdatePwd || !vo.user.id" v-model="vo.user.password" type="password" />
                            <i-switch v-show="vo.user.id" v-model.trim="isUpdatePwd"></i-switch>
                        </Form-item>
                        <Form-item label="姓名" prop="user.nickname">
                            <Input v-model.trim="vo.user.nickname" />
                        </Form-item>
                        <FormItem label="用户角色" prop="roles">
                            <Select transfer v-model="vo.roles" filterable multiple>
                                <Option v-for="item in roles" :value="item.id" :key="item.id">{{ item.name }}</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </vue-scroll>
            </div>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { UserType } from '@/libs/dic';
export default {
    name: 'User',
    mixins: [ TableColRender, TableDataView, ModelView ],
    data () {
        return {
            roles: [],
            isUpdatePwd: false,
            defaultCols: [{
                title: '用户名',
                key: 'username'
            }, {
                title: '姓名',
                key: 'nickname'
            }, {
                title: '类型',
                key: 'type',
                render: (h, params) => this.renderDic(h, params)(UserType)
            }, {
                title: '状态',
                key: 'enable',
                render: this.renderStatus
            }, {
                title: '创建时间',
                key: 'createTime',
                render: this.renderDate
            }, {
                title: '更新时间',
                key: 'updateTime',
                render: this.renderDate
            }, {
                title: '操作',
                key: 'action',
                width: 200,
                align: 'center',
                render: (h, params) => this.renderActions(h, params, [{
                    config: { loading: this.loadingBtn, style: { marginRight: '5px' }, disabled: params.row.type === 0 },
                    click: () => this.update(params.row),
                    text: '修改'
                }, {
                    type: 'switch',
                    click: () => this.status(params.row),
                    bool: params.row.enable,
                    config: { style: { marginRight: '5px' }, disabled: params.row.type === 0 }
                }, {
                    type: 'delete',
                    config: { disabled: params.row.type === 0 },
                    click: () => this.remove(params.row)
                }])
            }],
            table: {
                url: '/api/user/list',
                query: {
                    nickname: null,
                    phone: null
                }
            },
            vo: {
                user: {
                    id: null,
                    username: null,
                    password: null,
                    nickname: null,
                    phone: null,
                    type: null
                },
                roles: []
            },
            validate: {
                'user.username': [{ required: true, trigger: 'blur', pattern: /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/, message: '请输入正确的手机号' }],
                'user.password': [{ required: true, trigger: 'blur', message: '密码不能为空' }],
                roles: [{ required: true, type: 'array', trigger: 'change', message: '请选择角色' }]
            },
            addUrl: '/api/user/add',
            updateUrl: '/api/user/update',
            removeUrl: '/api/user/remove'
        };
    },
    computed: {
        isUpdate () {
            return this.vo.user.id !== null && this.vo.user.id !== undefined;
        },
        cols () {
            const cols = [ ...this.defaultCols ];
            this.$store.state.user.info?.platform?.type === 0 && cols.splice(2, 0, {
                title: '所属机构',
                key: 'platforms',
                render: (h, params) => this.renderListPop(h, params, val => val ? String(val).split(',') : [])
            });
            return cols;
        },
        generateAddOrUpdate () {
            return { ...this.vo, phone: this.vo.username };
        }
    },
    methods: {
        async add () {
            await this.loadAllRoles();
            this.$super(ModelView).add();
            this.vo.user.id = null;
        },
        async update (item) {
            this.loadingBtn = true;
            this.isUpdatePwd = false;
            Object.keys(this.vo.user).forEach(key => this.vo.user[key] = item[key]);
            await this.loadAllRoles();
            await this.loadRolesByUser(item.id);
            this.vo.user.password = null;
            this.validate['user.password'][0].required = false;
            this.model = true;
        },
        async status (item) {
            const result = await this.fetch(`/api/user/status/${item.id}`, { method: 'post', params: { enable: !item.enable } });
            if (result.success) {
                this.doQuery();
            }
        },
        async loadAllRoles () {
            const result = await this.get('/api/permission/role/list', { type: 2 });
            if (result.success) {
                this.roles = result.value;
            }
        },
        async loadRolesByUser (user) {
            const result = await this.get(`/api/permission/role/list/${user}`);
            if (result.success) {
                this.vo.roles = result.value.map(r => r.id);
            }
        }
    }
};
</script>
